@import url("reset.scss");

@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;600&display=swap");

.container-auth {
  font-family: "Bitter", serif !important;
  min-height: 100vh;
  background-image: linear-gradient(to bottom right, #beebf1, #f3accc);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
}

/* Login Card */
.login-card {
  width: 450px;
  background: rgba(255, 255, 255, 0.5);
  padding: 4rem;
  position: relative;
  backdrop-filter: blur(10px);
  background-color: rgba(255, 255, 255, 0.4);
  border-radius: 50px;
  box-shadow: 0px 35px 68px 0px rgba(145, 192, 255, 0.5),
    inset 0px -11px 16px 0px rgba(145, 192, 255, 0.6),
    inset 0px 11px 28px 0px rgb(255, 255, 255);
}

.login-card::before {
  content: "";
  position: absolute;
  inset: 0;
  background: rgba(255, 255, 255, 0.15);
  transform: rotate(-6deg);
  z-index: -1;
  backdrop-filter: blur(10px);
  background-color: rgba(255, 255, 255, 0.4);
  border-radius: 50px;
  box-shadow: 0px 35px 68px 0px rgba(145, 192, 255, 0.5),
    inset 0px -11px 16px 0px rgba(145, 192, 255, 0.6),
    inset 0px 11px 28px 0px rgb(255, 255, 255);
}
/* ---------- */

/* Login Card Logo */
.login-card-logo {
  margin-bottom: 2rem;
}

.login-card-logo img {
  width: 183px;
}
/* ---------- */

/* Login Card Standard */
.login-card-logo,
.login-card-header,
.login-card-footer {
  text-align: center;
}

.login-card a {
  text-decoration: none;
  color: #35339a;
}

.login-card a:hover {
  text-decoration: underline;
}
/* ---------- */

/* Login Card Header */
.login-card-header {
  margin-bottom: 2rem;
}

.login-card-header h1 {
  font-size: 2rem;
  font-weight: 600;
  margin-bottom: 0.5rem;
}

.login-card-header h1 + div {
  font-size: calc(1rem * 0.8);
  opacity: 0.8;
}
/* ---------- */

/* Login Card Form */
.login-card-form {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.login-card-form .form-item {
  position: relative;
}

.login-card-form .form-item .form-item-icon {
  position: absolute;
  top: 0.82rem;
  left: 1.4rem;
  font-size: 1.3rem;
  opacity: 0.4;
}

.login-card-form .checkbox {
  display: flex;
  align-items: center;
  gap: 7px;
}

.login-card-form .form-item-other {
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: calc(1rem * 0.8);
  margin-bottom: 0.5rem;
}
/* ---------- */

/* Login Card Footer */
.login-card-footer {
  margin-top: 1.5rem;
  font-size: calc(1rem * 0.8);
}
/* ---------- */

/* Login Card Form Elements */
.login-card input[type="text"],
.login-card input[type="password"],
.login-card input[type="email"] {
  border: none;
  outline: none;
  background: rgba(255, 255, 255, 0.3);
  padding: 1rem 1.5rem;
  padding-left: calc(1rem * 3.5);
  border-radius: 100px;
  width: 100%;
  transition: background 0.5s;
}

.login-card input:focus {
  -webkit-backdrop-filter: blur(4px px);
  backdrop-filter: blur(4px px);
  background-color: rgb(255, 255, 255);
  border-radius: 50px;
  box-shadow: 35px 15px 79px 0px rgb(239 138 182 / 50%),
    inset -1px -3px 30px 3px rgb(239 138 182 / 60%),
    inset 0px 11px 0px 0px rgb(255 255 255);
}

.login-card input[type="checkbox"] {
  width: 16px;
  height: 16px;
  accent-color: black;
}

.login-card button {
  background: black;
  color: white;
  padding: 1rem;
  border-radius: 100px;
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 2px;
  transition: background 0.5s;
}

.login-card button:hover {
  background-color: rgba(0, 0, 0, 0.85);
  cursor: pointer;
}
/* ---------- */

/* Login Card Social Buttons */
.login-card-social {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
  text-align: center;
  margin-top: 3rem;
}

.login-card-social > div {
  opacity: 0.8;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-size: calc(1rem * 0.8);
}

.login-card-social-btns {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1rem;
}

.login-card-social-btns a {
  display: flex;
  align-items: center;
  justify-content: center;
  color: black;
  width: 50px;
  height: 50px;
  background-color: rgba(255, 255, 255, 0.6);
  border-radius: 100px;
  transition: all 0.5s;
}

.login-card-social-btns a:hover {
  background-color: white;
  transform: scale(1.1);
}
/* ---------- */

/* Responsive */
@media (max-width: 768px) {
  body {
    padding: 2rem 0;
  }

  .login-card {
    width: 280px;
    padding: 2rem;
  }
}
